import * as os from 'os';
import * as path from 'path';

/**
 * Platform detection utilities for gamestore extensions
 */

export function getPlatform(): string {
  return process.platform;
}

export function isWindows(): boolean {
  return process.platform === 'win32';
}

export function isMacOS(): boolean {
  return process.platform === 'darwin';
}

export function isLinux(): boolean {
  return process.platform === 'linux';
}

export function isUnixLike(): boolean {
  return process.platform !== 'win32';
}

export function getExecutableExtension(): string {
  return isWindows() ? '.exe' : '';
}

export function getPathSeparator(): string {
  return isWindows() ? '\\' : '/';
}

export function getLineEnding(): string {
  return isWindows() ? '\r\n' : '\n';
}

export function getWineDriveCPath(): string {
  return path.join(os.homedir(), '.wine', 'drive_c');
}

export function platformSwitch<T>(cases: { windows?: T; macos?: T; linux?: T; default?: T }): T {
  if (isWindows() && cases.windows !== undefined) return cases.windows;
  if (isMacOS() && cases.macos !== undefined) return cases.macos;
  if (isLinux() && cases.linux !== undefined) return cases.linux;
  if (cases.default !== undefined) return cases.default;
  throw new Error('No matching platform case and no default provided');
}

export function normalizePath(inputPath: string): string {
  return path.normalize(inputPath).replace(/\\/g, '/');
}
